    position: fixed;
    width:0%;
    top: 0;
    bottom: 0;
    background: #ffb7ae;
    overflow: hidden;
    z-index:100;
}

#content{
    overflow-x: hidden;
}

.gallery{
    margin-bottom: 90px;
    img{
        width:100%;
        height: 100%;
        object-fit: cover;
    }
    a{
        position: relative;
        height:100%;
        display: block;
    }
    div{
        padding: 0;
    }
    .text-hover{
        opacity:0;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
        color:#fff;
        font-size: 18px;
        font-weight: 500;
        transition: opacity 0.1s ease;
        z-index: 30;
    }
}


.hover-box{
    span{
        transition: all ease 0.3s;
    }
    .top-side{
        position: absolute;
        top: 0;
        height: 0;
        background: lightcoral;
        width:100%;
        left:50%;
        transform: translateX(-50%);
        z-index: 10;
    }
    .right-side{
        position: absolute;
        right: 0;
        height: 100%;
        width:0;
        top:0;
        z-index: 10;
        background: lightcoral;
    }
    .left-side{
        position: absolute;
        left: 0;
        height: 100%;
        width:0%;
        z-index: 10;
        top:0;
        background: lightcoral;
    }
    .bottom-side{
        position: absolute;
        bottom: 0;
        height: 0;
        width:100%;
        z-index: 10;
        // left:50%;
        // transform: translateX(-50%);
        background: lightcoral;
    }
}


.gallery a:hover .top-side{
    height: 50%;
}

.gallery a:hover .bottom-side{
    height: 50%;
}


.gallery a:hover .right-side{
    width: 50%;
}

.gallery a:hover .left-side{
    width: 50%;
}

.gallery a:hover .text-hover{
    opacity:1;
    transition: opacity 0.3s ease-in 0.2s;
}